<template>
  <div>
    <el-container>
      <el-header
        style="padding: 0px;display:flex;justify-content:space-between;align-items: center"
      >
        <div style="display: inline">
          <el-input
            placeholder=",..."
            clearable
            @change="keywordsChange"
            style="width: 300px;margin: 0px;padding: 0px;"
            size="mini"
            :disabled="advanceSearchViewVisible"
            @keyup.enter.native="searchEmp"
            prefix-icon="el-icon-search"
            v-model="keywords"
          ></el-input>
          <el-button
            type="primary"
            size="mini"
            style="margin-left: 5px"
            icon="el-icon-search"
            @click="searchEmp"
          ></el-button>
          <el-button
            slot="reference"
            type="primary"
            size="mini"
            style="margin-left: 5px"
            @click="showAdvanceSearchView"
          >
            <i
              class="fa fa-lg"
              v-bind:class="[advanceSearchViewVisible ? faangledoubleup:faangledoubledown]"
              style="margin-right: 5px"
            ></i>
          </el-button>
        </div>
        <div style="margin-left: 5px;margin-right: 20px;display: inline">
          <el-upload
            :show-file-list="false"
            accept="application/vnd.ms-excel"
            action="/system/hr/importUsers"
            :on-success="fileUploadSuccess"
            :on-error="fileUploadError"
            :disabled="fileUploadBtnText==''"
            :before-upload="beforeFileUpload"
            style="display: inline"
          >
            <el-button size="mini" type="success" :loading="fileUploadBtnText==''">
              <i class="fa fa-lg fa-level-up" style="margin-right: 5px"></i>
              {{fileUploadBtnText}}
            </el-button>
          </el-upload>
          <el-button type="success" size="mini" @click="exportUsers">
            <i class="fa fa-lg fa-level-down" style="margin-right: 5px"></i>
          </el-button>
          <el-button type="primary" size="mini" icon="el-icon-plus" @click="showAddEmpView"></el-button>
        </div>
      </el-header>
      <el-main style="padding-left: 0px;padding-top: 0px">
        <div>
          <transition name="slide-fade">
            <div
              style="margin-bottom: 10px;border: 1px;border-radius: 5px;border-style: solid;padding: 5px 0px 5px 0px;box-sizing:border-box;border-color: #20a0ff"
              v-show="advanceSearchViewVisible"
            >
              <el-row></el-row>
              <el-row style="margin-top: 10px">
                <el-col :span="4">
                  :
                  <el-select
                    v-model="hr.nationId"
                    style="width: 130px"
                    size="mini"
                    placeholder=""
                  >
                    <el-option
                      v-for="item in nations"
                      :key="item.id"
                      :label="item.name"
                      :value="item.id"
                    ></el-option>
                  </el-select>
                </el-col>

                <el-col :span="5">
                  :
                  <el-popover
                    v-model="showOrHidePop2"
                    placement="right"
                    title=""
                    trigger="manual"
                  >
                    <el-tree
                      :data="deps"
                      :default-expand-all="false"
                      :props="defaultProps"
                      :expand-on-click-node="false"
                      @node-click="handleNodeClick2"
                    ></el-tree>
                    <div
                      slot="reference"
                      style="width: 130px;height: 26px;display: inline-flex;font-size:13px;border: 1px;border-radius: 5px;border-style: solid;padding-left: 13px;box-sizing:border-box;border-color: #dcdfe6;cursor: pointer;align-items: center"
                      @click="showDepTree2"
                      v-bind:style="{color: depTextColor}"
                    >{{hr.departmentName}}</div>
                  </el-popover>
                </el-col>

                <el-col :span="5" :offset="4">
                  <el-button size="mini" @click="cancelSearch"></el-button>
                  <el-button icon="el-icon-search" type="primary" size="mini" @click="searchEmp"></el-button>
                </el-col>
              </el-row>
            </div>
          </transition>
          <el-table
            :data="hrs"
            v-loading="tableLoading"
            border
            stripe
            @selection-change="handleSelectionChange"
            size="mini"
            style="width: 100%"
          >
            <el-table-column type="selection" align="left" width="30"></el-table-column>
            <el-table-column prop="name" align="left" fixed label="" width="90"></el-table-column>

            <el-table-column prop="gender" label="" width="50"></el-table-column>

            <el-table-column prop="idCard" width="150" align="left" label=""></el-table-column>

            <el-table-column width="60" prop="nation.name" label=""></el-table-column>

            <el-table-column prop="phone" width="100" label=""></el-table-column>

            <el-table-column prop="department.name" align="left" width="100" label=""></el-table-column>
            <el-table-column width="100" align="left" prop="roles[0].nameZh" label=""></el-table-column>

            <el-table-column width="85" align="left" label="">
              <template slot-scope="scope">{{ scope.row.birthday | formatDate}}</template>
            </el-table-column>
            <el-table-column prop="address" width="220" align="left" label=""></el-table-column>
            <el-table-column prop="email" width="180" align="left" label=""></el-table-column>

            <el-table-column fixed="right" label="" width="195">
              <template slot-scope="scope">
                <el-button
                  @click="showEditEmpView(scope.row)"
                  style="padding: 3px 4px 3px 4px;margin: 2px"
                  size="mini"
                ></el-button>
                <el-button
                  @click="showaddRoleView(scope.row)"
                  style="padding: 3px 4px 3px 4px;margin: 2px"
                  type="primary"
                  size="mini"
                ></el-button>
                <el-button
                  type="danger"
                  style="padding: 3px 4px 3px 4px;margin: 2px"
                  size="mini"
                  @click="deleteEmp(scope.row)"
                ></el-button>
              </template>
            </el-table-column>
          </el-table>
          <div style="display: flex;justify-content: space-between;margin: 2px">
            <el-button
              type="danger"
              size="mini"
              v-if="hrs.length>0"
              :disabled="multipleSelection.length==0"
              @click="deleteManyEmps"
            ></el-button>
          <el-pagination
              background
              @size-change="handleSizeChange"
              @current-change="currentChange"
              :current-page="currentPage"
              :page-sizes="[10, 15, 20, 30, 50]"
              :page-size="10"
              layout="total, sizes, prev, pager, next, jumper"
              :total="totalCount"
            ></el-pagination>
          </div>
        </div>
      </el-main>
    </el-container>
    <el-form :model="user" :rules="rules" ref="editUserForm" style="margin: 0px;padding: 0px;">
      <div style="text-align: left">
        <el-dialog
          :title="dialogTitle"
          style="padding: 0px;"
          :close-on-click-modal="false"
          :visible.sync="dialogVisible"
          width="77%"
        >
          <el-row>
            <el-col :span="6">
              <div>
                <el-form-item label=":" prop="name">
                  <el-input
                    prefix-icon="el-icon-edit"
                    v-model="user.name"
                    size="mini"
                    style="width: 150px"
                    placeholder=""
                  ></el-input>
                </el-form-item>
              </div>
            </el-col>
            <el-col :span="5">
              <div>
                <el-form-item label=":" prop="gender">
                  <el-radio-group v-model="user.gender">
                    <el-radio label=""></el-radio>
                    <el-radio style="margin-left: 15px" label=""></el-radio>
                  </el-radio-group>
                </el-form-item>
              </div>
            </el-col>
            <el-col :span="6">
              <div>
                <el-form-item label=":" prop="birthday">
                  <el-date-picker
                    v-model="user.birthday"
                    size="mini"
                    value-format="yyyy-MM-dd HH:mm:ss"
                    style="width: 150px"
                    type="date"
                    placeholder=""
                  ></el-date-picker>
                </el-form-item>
              </div>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="6">
              <div>
                <el-form-item label=":" prop="nationId">
                  <el-select
                    v-model="user.nationId"
                    style="width: 150px"
                    size="mini"
                    placeholder=""
                  >
                    <el-option
                      v-for="item in nations"
                      :key="item.id"
                      :label="item.name"
                      :value="item.id"
                    ></el-option>
                  </el-select>
                </el-form-item>
              </div>
            </el-col>

            <el-col :span="7">
              <div>
                <el-form-item label=":" prop="address">
                  <el-input
                    prefix-icon="el-icon-edit"
                    v-model="user.address"
                    size="mini"
                    style="width: 200px"
                    placeholder="..."
                  ></el-input>
                </el-form-item>
              </div>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="6">
              <div>
                <el-form-item label=":" prop="departmentId">
                  <el-popover
                    v-model="showOrHidePop"
                    placement="right"
                    title=""
                    trigger="manual"
                  >
                    <el-tree
                      :data="deps"
                      :default-expand-all="false"
                      :props="defaultProps"
                      :expand-on-click-node="false"
                      @node-click="handleNodeClick"
                    ></el-tree>
                    <div
                      slot="reference"
                      style="width: 150px;height: 26px;display: inline-flex;font-size:13px;border: 1px;border-radius: 5px;border-style: solid;padding-left: 13px;box-sizing:border-box;border-color: #dcdfe6;cursor: pointer;align-items: center"
                      @click.left="showDepTree"
                      v-bind:style="{color: depTextColor}"
                    >{{user.departmentName}}</div>
                  </el-popover>
                </el-form-item>
              </div>
            </el-col>
            <el-col :span="7">
              <div>
                <el-form-item label=":" prop="phone">
                  <el-input
                    prefix-icon="el-icon-phone"
                    v-model="user.phone"
                    size="mini"
                    style="width: 200px"
                    placeholder="..."
                  ></el-input>
                </el-form-item>
              </div>
            </el-col>
          </el-row>

          <el-row>
            <el-col :span="8">
              <div>
                <el-form-item label=":" prop="idCard">
                  <el-input
                    prefix-icon="el-icon-edit"
                    v-model="user.idCard"
                    size="mini"
                    style="width: 180px"
                    placeholder="..."
                  ></el-input>
                </el-form-item>
              </div>
            </el-col>

            <el-col :span="8">
              <div>
                <el-form-item label=":" prop="email">
                  <el-input
                    prefix-icon="el-icon-edit"
                    v-model="user.email"
                    size="mini"
                    style="width: 180px"
                    placeholder="..."
                  ></el-input>
                </el-form-item>
              </div>
            </el-col>
          </el-row>
          <span slot="footer" class="dialog-footer">
            <el-button size="mini" @click="cancelEidt"> </el-button>
            <el-button size="mini" type="primary" @click="addEmp('editUserForm')"> </el-button>
          </span>
        </el-dialog>
      </div>
    </el-form>

    <el-form :model="hr" :rules="rules" ref="addUserForm" style="margin: 0px;padding: 0px;">
      <div style="text-align: left">
        <el-dialog
          :title="dialogTitle"
          style="padding: 0px;"
          :close-on-click-modal="false"
          :visible.sync="dialogVisible1"
          width="77%"
        >
          <el-row>
            <el-col :span="6">
              <div>
                <el-form-item label=":" prop="name">
                  <el-input
                    prefix-icon="el-icon-edit"
                    v-model="hr.name"
                    size="mini"
                    style="width: 150px"
                    placeholder=""
                  ></el-input>
                </el-form-item>
              </div>
            </el-col>
            <el-col :span="5">
              <div>
                <el-form-item label=":" prop="gender">
                  <el-radio-group v-model="hr.gender">
                    <el-radio label=""></el-radio>
                    <el-radio style="margin-left: 15px" label=""></el-radio>
                  </el-radio-group>
                </el-form-item>
              </div>
            </el-col>
            <el-col :span="6">
              <div>
                <el-form-item label=":" prop="birthday">
                  <el-date-picker
                    v-model="hr.birthday"
                    size="mini"
                    value-format="yyyy-MM-dd HH:mm:ss"
                    style="width: 150px"
                    type="date"
                    placeholder=""
                  ></el-date-picker>
                </el-form-item>
              </div>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="6">
              <div>
                <el-form-item label=":" prop="nationId">
                  <el-select
                    v-model="hr.nationId"
                    style="width: 150px"
                    size="mini"
                    placeholder=""
                  >
                    <el-option
                      v-for="item in nations"
                      :key="item.id"
                      :label="item.name"
                      :value="item.id"
                    ></el-option>
                  </el-select>
                </el-form-item>
              </div>
            </el-col>

            <el-col :span="7">
              <div>
                <el-form-item label=":" prop="address">
                  <el-input
                    prefix-icon="el-icon-edit"
                    v-model="hr.address"
                    size="mini"
                    style="width: 200px"
                    placeholder="..."
                  ></el-input>
                </el-form-item>
              </div>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="6">
              <div>
                <el-form-item label=":" prop="departmentId">
                  <el-popover
                    v-model="showOrHidePop"
                    placement="right"
                    title=""
                    trigger="manual"
                  >
                    <el-tree
                      :data="deps"
                      :default-expand-all="false"
                      :props="defaultProps"
                      :expand-on-click-node="false"
                      @node-click="handleNodeClick"
                    ></el-tree>
                    <div
                      slot="reference"
                      style="width: 150px;height: 26px;display: inline-flex;font-size:13px;border: 1px;border-radius: 5px;border-style: solid;padding-left: 13px;box-sizing:border-box;border-color: #dcdfe6;cursor: pointer;align-items: center"
                      @click.left="showDepTree"
                      v-bind:style="{color: depTextColor}"
                    >{{hr.departmentName}}</div>
                  </el-popover>
                </el-form-item>
              </div>
            </el-col>
            <el-col :span="7">
              <div>
                <el-form-item label=":" prop="phone">
                  <el-input
                    prefix-icon="el-icon-phone"
                    v-model="hr.phone"
                    size="mini"
                    style="width: 200px"
                    placeholder="..."
                  ></el-input>
                </el-form-item>
              </div>
            </el-col>
          </el-row>

          <el-row>
            <el-col :span="8">
              <div>
                <el-form-item label=":" prop="idCard">
                  <el-input
                    prefix-icon="el-icon-edit"
                    v-model="hr.idCard"
                    size="mini"
                    style="width: 180px"
                    placeholder="..."
                  ></el-input>
                </el-form-item>
              </div>
            </el-col>

            <el-col :span="8">
              <div>
                <el-form-item label=":" prop="email">
                  <el-input
                    prefix-icon="el-icon-edit"
                    v-model="hr.email"
                    size="mini"
                    style="width: 180px"
                    placeholder="..."
                  ></el-input>
                </el-form-item>
              </div>
            </el-col>
          </el-row>
          <span slot="footer" class="dialog-footer">
            <el-button size="mini" @click="cancelEidt"> </el-button>
            <el-button size="mini" type="primary" @click="addUser()"> </el-button>
          </span>
        </el-dialog>
      </div>
    </el-form>

    <div style="text-align: left">
      <el-dialog title="" :visible.sync="dialogVisible3" width="25%">
        <div class="user-info">
          :
          <el-tag
            v-for="role in roleAll"
            :key="role.id"
            type="success"
            size="mini"
            style="margin-right: 5px"
            :disable-transitions="false"
          >{{role.nameZh}}</el-tag>
          <el-popover
            placement="right"
            title=""
            width="300"
            @hide="updateHrRoles(userId)"
            :key="role.id"
            trigger="click"
          >
            <el-select v-model="selRoles" multiple placeholder="">
              <el-option v-for="ar in role" :key="ar.id" :label="ar.nameZh" :value="ar.id"></el-option>
            </el-select>
            <el-button
              type="text"
              icon="el-icon-more"
              style="color: #09c0f6;padding-top: 0px"
              slot="reference"
              @click="loadSelRoles(role,userId)"
              :disabled="moreBtnState"
            ></el-button>
          </el-popover>
        </div>
        <!-- <div>
          <span></span>
          <el-select
            @change="selectGet"
            v-model="rid"
            style="width: 200px"
            placeholder=""
            size="mini"
          >
            <el-option v-for="item in role" :key="item.id" :label="item.nameZh" :value="item.id"></el-option>
          </el-select>
        </div>

        <span slot="footer" class="dialog-footer">
          <el-button size="small" @click="dialogVisible3 = false"></el-button>
          <el-button size="small" type="primary" @click="addRole(rid)"></el-button>
        </span>-->
      </el-dialog>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      emps: [],
      hrs: [],
      keywords: "",
      fileUploadBtnText: "",
      beginDateScope: "",
      faangledoubleup: "fa-angle-double-up",
      faangledoubledown: "fa-angle-double-down",
      dialogTitle: "",
      multipleSelection: [],
      depTextColor: "#c0c4cc",
      nations: [],
      politics: [],
      positions: [],
      joblevels: [],
      cardLoading: [],
      totalCount: -1,
      currentPage: 1,
      obj: {},

      fullloading: false,
      cardLoading: [],
      eploading: [],
      allRoles: [],
      moreBtnState: false,
      selRoles: [],
      selRolesBak: [],
      rid: "",

      rolelist: "",

      deps: [],
      roles: [],
      defaultProps: {
        label: "name",
        isLeaf: "leaf",
        children: "children"
      },
      dialogVisible: false,
      dialogVisible1: false,
      dialogVisible3: false,
      tableLoading: false,
      advanceSearchViewVisible: false,
      showOrHidePop: false,
      showOrHidePop2: false,
      hr: {
        id: "",
        name: "",
        phone: "",
        gender: "",
        address: "",
        departmentId: "",
        enabled: "",
        username: "",
        password: "",
        userface: "",
        idCard: "",
        remark: "",
        birthday: "",
        email: "",
        nationId: "",
        departmentName: "..."
      },
      role: {
        id: "",
        name: "",
        nameZh: ""
      },
      user: {
        id: "",
        name: "",
        phone: "",
        gender: "",
        address: "",
        departmentId: "",
        enabled: "",
        username: "",
        password: "",
        userface: "",
        idCard: "",
        remark: "",
        birthday: "",
        nationId: "",
        email: "",
        departmentName: "..."
      },
      rules: {
        name: [{ required: true, message: ":", trigger: "blur" }],
        gender: [{ required: true, message: ":", trigger: "blur" }],
        birthday: [
          { required: true, message: ":", trigger: "blur" }
        ],
        idCard: [
          {
            required: true,
            message: ":",
            trigger: "blur"
          },
          {
            pattern: /(^[1-9]\d{5}(18|19|([23]\d))\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{3}[0-9Xx]$)|(^[1-9]\d{5}\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{2}$)/,
            message: "",
            trigger: "blur"
          }
        ],

        phone: [{ required: true, message: ":", trigger: "blur" }],
        address: [
          { required: true, message: ":", trigger: "blur" }
        ],
        departmentId: [
          { required: true, message: ":", trigger: "change" }
        ]
      }
    };
  },
  computed: {
    userhr() {
      return this.$store.state.user;
    }
  },
  mounted: function() {
    this.initData();
    this.loadEmps();
    this.initCards();
    this.loadAllRoles();
  },
  methods: {
    updateHrRoles(hrId) {
      this.moreBtnState = false;
      var _this = this;
      if (this.selRolesBak.length == this.selRoles.length) {
        for (var i = 0; i < this.selRoles.length; i++) {
          for (var j = 0; j < this.selRolesBak.length; j++) {
            if (this.selRoles[i] == this.selRolesBak[j]) {
              this.selRolesBak.splice(j, 1);
              break;
            }
          }
        }
        if (this.selRolesBak.length == 0) {
          return;
        }
      }
      this.putRequest("/system/hr/roles", {
        hrId: hrId,
        rids: this.selRoles
      }).then(resp => {
        // _this.eploading.splice(index, 1, false);
        if (resp && resp.status == 200) {
          var data = resp.data;

          if (data.status == "success") {
            this.loadEmps();
          }
        }
      });
    },
    refreshHr(hrId) {
      var _this = this;
      // _this.cardLoading.splice(index, 1, true);
      this.putRequest("/system/hr/id/" + hrId).then(resp => {
        // _this.cardLoading.splice(index, 1, false);
        // _this.hrs.splice(index, 1, resp.data);
      });
    },
    loadSelRoles(hrRoles, index) {
      this.moreBtnState = true;
      this.selRoles = [];
      this.selRolesBak = [];
      hrRoles.forEach(role => {
        this.selRoles.push(role.id);
        this.selRolesBak.push(role.id);
      });
    },
    loadAllRoles() {
      var _this = this;
      this.getRequest("/system/basic/roles").then(resp => {
        _this.fullloading = false;
        if (resp && resp.status == 200) {
          _this.allRoles = resp.data;
        }
      });
    },
    searchClick() {
      this.initCards();
      this.loadAllRoles();
    },
    fileUploadSuccess(response, file, fileList) {
      if (response) {
        this.$message({ type: response.status, message: response.msg });
      }
      this.loadEmps();
      this.fileUploadBtnText = "";
    },
    fileUploadError(err, file, fileList) {
      this.$message({ type: "error", message: "!" });
      this.fileUploadBtnText = "";
    },
    beforeFileUpload(file) {
      this.fileUploadBtnText = "";
    },
    exportUsers() {
      window.open("/system/hr/exportUsers", "_parent");
    },
    cancelSearch() {
      this.advanceSearchViewVisible = false;
      this.emptyEmpData();
      this.beginDateScope = "";
      this.loadEmps();
    },
    showAdvanceSearchView() {
      this.advanceSearchViewVisible = !this.advanceSearchViewVisible;
      this.keywords = "";
      if (!this.advanceSearchViewVisible) {
        this.emptyEmpData();
        this.beginDateScope = "";
        this.loadEmps();
      }
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    deleteManyEmps() {
      this.$confirm(
        "[" + this.multipleSelection.length + "], ?",
        "",
        {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning"
        }
      )
        .then(() => {
          var ids = "";
          for (var i = 0; i < this.multipleSelection.length; i++) {
            ids += this.multipleSelection[i].id + ",";
          }
          this.doDelete(ids);
        })
        .catch(() => {});
    },
    deleteEmp(row) {
      this.$confirm("[" + row.name + "], ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          this.doDelete(row.id);
        })
        .catch(() => {});
    },
    doDelete(ids) {
      this.tableLoading = true;
      var _this = this;
      this.deleteRequest("/system/hr/" + ids).then(resp => {
        _this.tableLoading = false;
        if (resp && resp.status == 200) {
          var data = resp.data;

          _this.loadEmps();
        }
      });
    },
    keywordsChange(val) {
      if (val == "") {
        this.loadEmps();
      }
    },
    searchEmp() {
      this.loadEmps();
    },
    currentChange(currentChange) {
      this.currentPage = currentChange;
      this.loadEmps();
    },
    loadEmps() {
      var _this = this;
      this.tableLoading = true;

      var searchWords;
      if (this.keywords === "") {
        searchWords = "all";
      } else {
        searchWords = this.keywords;
      }
      this.getRequest(
        "/system/hr/?page=" +
          (this.currentPage - 1) +
          "&size=10&keywords=" +
          this.keywords +
          "&nationId=" +
          this.hr.nationId +
          "&departmentId=" +
          this.hr.departmentId +
          "&upid=" +
          this.userhr.departmentId +
          "&nameZh=" +
          ""
      ).then(resp => {
        this.tableLoading = false;
        var i;
        if (resp && resp.status == 200) {
          var data = resp.data;
          _this.hrs = data.hrs;
          _this.roles = data.roles;

         
          _this.nation = data.nation;
          _this.totalCount = data.count;
          //            _this.emptyEmpData();
           for (i = 0; i < _this.roles.length; i++) {
            _this.rolelist += _this.roles[i].nameZh;
          }
          console.log(_this.rolelist)

          console.log(data);
        }
      });
    },
    addEmp(formName) {
      var _this = this;
      this.$refs[formName].validate(valid => {
        if (valid) {
          if (this.hr.id) {
            //
            this.tableLoading = true;
            this.putRequest("/system/hr/", this.user).then(resp => {
              _this.tableLoading = false;
              this.loadEmps();
              if (resp && resp.status == 200) {
                var data = resp.data;

                _this.dialogVisible = false;
                this.emptyEmpData();

                this.loadEmps();
              }
            });
          } else {
            //
            this.tableLoading = true;
            this.postRequest("/system/hr/", this.hr).then(resp => {
              _this.tableLoading = false;
              if (resp && resp.status == 200) {
                var data = resp.data;

                _this.dialogVisible1 = false;
                _this.emptyEmpData();
                _this.loadEmps();
              }
            });
          }
        } else {
          return false;
        }
      });
    },
    addUser() {
      var _this = this;
      this.tableLoading = true;
      this.postRequest("/system/hr/", this.hr).then(resp => {
        _this.tableLoading = false;
        if (resp && resp.status == 200) {
          var data = resp.data;

          _this.dialogVisible1 = false;
          _this.emptyEmpData();
          _this.loadEmps();
        }
      });
    },
    addRole(rid) {
      var _this = this;
      this.dialogVisible3 = false;
      this.treeLoading = true;
      this.putRequest("/system/hr/roles", {
        hrId: _this.hrId,
        rids: [rid]
      }).then(resp => {
        // _this.eploading.splice(index, 1, false);
        if (resp && resp.status == 200) {
          var data = resp.data;
          this.emptyEmpData();
          _this.loadEmps();
        }
      });
    },
    selectGet(vId) {
      let obj = {};
      obj = this.role.find(item => {
        //selectList
        return item.id === vId; //
      });
      return obj.id;
      console.log(obj.name); //namelabel
      console.log(obj.id);
    },
    showaddRoleView(row) {
      var _this = this;
      // this.loadAllDeps();
      this.dialogVisible3 = true;
      console.log(row);

      _this.hrId = row.id;
      this.userId = row.id;
      this.roleAll = row.roles;
      console.log(role);

      // _this.rids = []

      event.stopPropagation();
    },

    reg() {
      this.postRequest("/system/hr/hr/reg", {
        username: 123,
        password: 123
      }).then(resp => {
        if (resp && resp.status == 200) {
          var data = resp.data;
          console.log(data);
        }
      });
    },
    cancelEidt() {
      this.dialogVisible = false;
      this.dialogVisible1 = false;
      this.emptyEmpData();
    },
    showDepTree() {
      this.showOrHidePop = !this.showOrHidePop;
    },
    showDepTree2() {
      this.showOrHidePop2 = !this.showOrHidePop2;
    },
    handleNodeClick(data) {
      this.hr.departmentName = data.name;
      this.hr.departmentId = data.id;
      this.user.departmentId = this.hr.departmentId;
      this.user.departmentName = this.hr.departmentName;
      this.showOrHidePop = false;
      this.depTextColor = "#606266";
    },
    handleNodeClick2(data) {
      this.hr.departmentName = data.name;
      this.hr.departmentId = data.id;
      this.showOrHidePop2 = false;
      this.depTextColor = "#606266";
    },
    initData() {
      var _this = this;
      this.getRequest("/system/hr/basicdata").then(resp => {
        if (resp && resp.status == 200) {
          var data = resp.data;
          _this.nations = data.nations;
          _this.politics = data.politics;
          _this.deps = data.deps;
          _this.positions = data.positions;
          _this.role = data.roles;
        }
      });
    },
    showEditEmpView(row) {
      console.log(row);
      this.dialogTitle = "";
      this.hr = row;
      this.hr.birthday = this.formatDate(row.birthday);

      this.hr.nationId = row.nation.id;
      this.hr.departmentId = row.department.id;
      this.hr.departmentName = row.department.name;
      this.dialogVisible = true;

      this.user.id = row.id;
      this.user.name = this.hr.name;
      this.user.gender = this.hr.gender;
      this.user.phone = this.hr.phone;
      this.user.address = this.hr.address;
      this.user.departmentId = this.hr.departmentId;
      this.user.departmentName = this.hr.departmentName;
      this.user.enabled = this.hr.enabled;
      this.user.idCard = this.hr.idCard;
      this.user.email = this.hr.email;
      this.user.birthday = this.hr.birthday;
      this.user.nationId = this.hr.nationId;
      this.user.username = this.hr.phone;
    },
    showAddEmpView() {
      this.dialogTitle = "";
      this.dialogVisible1 = true;
      var _this = this;

      _this.hr.enabled = true;
      _this.hr.username = "123";
      _this.hr.password = "123";
      _this.hr.remark = "1";
      _this.hr.userface = "";
    },
    emptyEmpData() {
      this.hr = {
        id: "",
        name: "",
        phone: "",
        gender: "",
        address: "",
        departmentId: "",
        enabled: true,
        username: "",
        password: "",
        userface: "",
        idCard: "",
        remark: "",
        birthday: "",
        email: "",
        nationId: "",

        departmentName: "..."
      };
      this.user = {
        // id: "",
        name: "",
        phone: "",
        gender: "",
        address: "",
        departmentId: "",
        // enabled: true,
        username: "",
        password: "",
        userface: "",
        idCard: "",
        remark: "",
        birthday: "",
        nationId: "",
        email: "",
        departmentName: "..."
      };
    }
  }
};
</script>
<style>
.el-dialog__body {
  padding-top: 0px;
  padding-bottom: 0px;
}

.slide-fade-enter-active {
  transition: all 0.8s ease;
}

.slide-fade-leave-active {
  transition: all 0.8s cubic-bezier(1, 0.5, 0.8, 1);
}

.slide-fade-enter,
.slide-fade-leave-to {
  transform: translateX(10px);
  opacity: 0;
}
.user-info {
  font-size: 12px;
  color: #09c0f6;
}
</style>



