<template>
  <div>
    <span></span>
    <hr>
    <div style="text-align: left">
      <el-input :placeholder="'...'" size="mini" style="width: 15%;" v-model="name"></el-input>
      <el-input :placeholder="'...'" size="mini" style="width: 12%;" v-model="point"></el-input>

      <el-button type="primary" icon="el-icon-plus" size="mini" @click="addPosition"></el-button>
    </div>
    <div style="margin-top: 10px">
      <el-table
        v-loading="loading"
        :data="catData"
        size="mini"
        stripe
        border
        @selection-change="handleSelectionChange"
        style="width: 80%"
      >
        <el-table-column type="selection" width="55" align="left"></el-table-column>
        <el-table-column prop="name" :label="''" width="180" align="left"></el-table-column>
        <el-table-column prop="point" :label="''" width="180" align="left"></el-table-column>

        <el-table-column label="" align="left">
          <template slot-scope="scope">
            <el-button size="mini" @click="handleEdit(scope.$index, scope.row)"></el-button>

            <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div style="text-align: left;margin-top: 10px">
      <el-button
        type="danger"
        size="mini"
        v-if="catData.length>0"
        :disabled="multipleSelection.length==0"
        @click="deleteMany"
      ></el-button>
    </div>
    <div style="text-align: left">
      <el-dialog :title="''" :visible.sync="dialogVisible" width="25%">
        <el-input v-model="pointoption.name" size placeholder="..."></el-input>
        <hr style="width:20%">
        <el-input v-model="pointoption.point" size placeholder="..."></el-input>

        <span slot="footer" class="dialog-footer">
          <el-button size="mini" @click="cancelEidt"> </el-button>
          <el-button type="primary" size="mini" @click="updatePosNameExec"> </el-button>
        </span>
      </el-dialog>
    </div>
  </div>
</template>
<script>
export default {
  mounted: function() {
    this.loadTableData();
  },
  methods: {
    updatePosNameExec() {
      if (!this.isNotNullORBlank(this.pointoption.name)) {
        this.$message.warning("!");
        return;
      }
      this.loading = true;
      var _this = this;
      this.putRequest("/pointoption/", {
        id:this.pointoption.id,
        name:this.pointoption.name,
        point:this.pointoption.point
      }).then(resp => {
        _this.loading = false;
        if (resp && resp.status == 200) {
          this.dialogVisible = false;
          var data = resp.data;
          _this.loadTableData();
        }
      });
    },
    deleteMany() {
      var _this = this;
      this.$confirm(
        "" + this.multipleSelection.length + ", ?",
        "",
        {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning"
        }
      )
        .then(() => {
          var multipleSelection = _this.multipleSelection;
          var ids = "";
          multipleSelection.forEach(row => {
            ids = ids + row.id + ",";
          });
          _this.doDelete(ids);
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        });
    },
    addPosition() {
      if (!this.isNotNullORBlank(this.name)) {
        this.$message.warning("!");
        return;
      }

      var _this = this;
      this.loading = true;
      this.postRequest("/pointoption/", {
        name: this.name,
        point: this.point
      }).then(resp => {
        _this.loading = false;
        if (resp && resp.status == 200) {
          var data = resp.data;

          _this.loadTableData();
          _this.name = "";
          _this.point = "";
        }
      });
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    cancelEidt() {
      this.dialogVisible = false;
      this.emptyEmpData();
    },
    handleEdit(index, row) {
      this.pointoption.name = row.name;
      this.pointoption.id = row.id;
      this.pointoption.point = row.point;
      this.dialogVisible = true;
    },
    handleDelete(index, row) {
      var _this = this;
      this.$confirm("[" + row.name + "], ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          _this.doDelete(row.id);
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        });
    },
    doDelete(ids) {
      var _this = this;
      _this.loading = true;
      var url = "/pointoption/";
      this.deleteRequest(url + ids).then(resp => {
        _this.loading = false;
        if (resp && resp.status == 200) {
          _this.loadTableData();
        }
      });
    },
    emptyEmpData() {
      this.point = "";
      this.name = "";
    },
    loadTableData() {
      var _this = this;
      this.loading = true;
      this.getRequest("/pointoption/all").then(resp => {
        _this.loading = false;
        if (resp && resp.status == 200) {
          _this.catData = resp.data;
        }
      });
    }
  },
  data() {
    return {
      id: "",
      name: "",
      point: "",
      updatePosId: -1,
      loading: false,
      dialogVisible: false,
      multipleSelection: [],
      type: [],
      nameLabelName: "",
      catData: [],
      pointoption: [],
      pointoption: {
        id: "",
        name: "",
        point: ""
      }
    };
  },
  props: ["state"]
};
</script>



