<template>
  <div>
    <span></span>
    <hr/>
    <!-- <div style="text-align: left">
      <el-input
        :placeholder="'...'"
        size="mini"
        @keyup.enter.native="addPosition"
        style="width: 300px;"
        prefix-icon="el-icon-plus"
        v-model="name"
      ></el-input>
      
      <el-button type="primary" icon="el-icon-plus" size="mini" @click="addPosition"></el-button>
    </div> -->
    <div style="margin-top: 10px">
      <el-table
        v-loading="loading"
        :data="menu"
        size="mini"
        stripe
        border
        @selection-change="handleSelectionChange"
        style="width: 80%"
      >
        <el-table-column type="selection" width="55" align="left"></el-table-column>
        <el-table-column prop="id" label="" width="80" align="left"></el-table-column>
        <el-table-column
          prop="name"
          :label="''"
          width="180"
          align="left"
        ></el-table-column>
  
        <el-table-column label="" align="left">
          <template slot-scope="scope">
            <el-button type="primary" size="mini" @click="handleEdit(scope.$index, scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
  
    <div style="text-align: left">
      <el-dialog
        :title="''"
        :visible.sync="dialogVisible"
        width="25%"
      >
        <el-input v-model="name" size="mini" placeholder="..."></el-input>
       
        <span slot="footer" class="dialog-footer">
          <el-button size="mini" @click="dialogVisible = false"> </el-button>
          <el-button type="primary" size="mini" @click="updatePosNameExec"> </el-button>
        </span>
      </el-dialog>
    </div>
  </div>
</template>
<script>
export default {
  mounted: function() {
    this.loadTableData();
  },
  methods: {
    updatePosNameExec() {
      if (!this.isNotNullORBlank(this.name)) {
        this.$message.warning(
          "!"
        );
        return;
      }
      this.loading = true;
      var _this = this;
      this.putRequest( "/system/basic/menu",
        {
          name: this.name,
          id: this.id,
        }
      ).then(resp => {
        _this.loading = false;
        if (resp && resp.status == 200) {
          this.dialogVisible = false;
          var data = resp.data;
          _this.loadTableData();
        }
      });
    },
   
   
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    handleEdit(index, row) {
      this.name = row.name;
      this.id = row.id;
      this.dialogVisible = true;
    },
   

    loadTableData() {
      var _this = this;
      this.loading = true;
      this.getRequest("/system/basic/menu").then(resp => {
        _this.loading = false;
        if (resp && resp.status == 200) {
          _this.menu = resp.data.menus;
        }
      });
    }
  },
  data() {
    return {
      id:"",
      name:"",
      date:"",
      updatePosId: -1,
      loading: false,
      dialogVisible: false,
      multipleSelection: [],
      type: [],
      nameLabelName: "",
      menu: []
    };
  },
  props: ["state"]
};
</script>



